@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,700;1,400;1,500;1,600&family=Nova+Script&family=Raleway:wght@300;600;700&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap');
.cartPage_Container {
    height: fit-content;
    width: 90%;
    display: grid;
    grid-template-columns: 60% 10% 30%;
    margin: 5rem 0rem 0rem 5rem;
  }
  .cartpage_image {
    width: 60%;
  }
  .cartpage_name {
    font-weight: 600;
    font-size: 2rem;
    line-height: 27px;
    margin: 0px 0px 16px;
    font-family: Raleway;
  }
  .cartpage_brand {
    font-weight: 400;
    font-size: 1.8rem;
    line-height: 27px;
    margin: 0px 0px 12px;
    font-family: Raleway;
  }
  .cartpage_amount {
    font-size: 1.3rem;
    font-weight: 700;
    line-height: 18px;
    margin: 0px 0px 12px;
  }
  .cartpage_quantityupdate {
    width: 50px;
    height: 50px;
    border: 1px solid black;
    background-color: #fff;
    font-size: 1.6rem;
    font-weight: 300;
    cursor: pointer;
    margin-top: 1rem;
  }
  .cartpage_total {
    margin-top: 4rem;
    margin-left: 3.5rem;
    font-size: 1.8rem;
    height: 60vh;
  }
  .cartpageheader_name {
    font-size: 2.3rem;
    font-weight: 600;
    margin: 1em 0 0 2em;
  }
  
  .cartpage_hr {
    width: 90%;
    opacity: 0.3;
    margin-top: 4rem;
  }
  .cartpage_innerCon {
    position: absolute;
    z-index: 1;
    width: 50%;
    height: 60px;
  }
  .cartpage_downCon {
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  .cartpage_quantityContainer {
    margin-right: 4rem;
    margin-top: 1rem;
    font-weight: 600;
    font-size: 1.3rem;
  }
  .tax_span {
    display: block;
    margin-top: 1rem;
  }
  .quantity_span {
    display: block;
    margin-top: 1rem;
  }
  .total_span {
    display: block;
    margin-top: 1rem;
  }
  .order_button {
    background: #5ece7b;
    border: none;
    margin-top: 2rem;
    color: #fff;
    display: block;
    width: 300px;
    height: 60px;
    cursor: pointer;
  }
  .carret_button {
    width: 34px;
    height: 34px;
     background: #000000BA;
     color:#fff;
     font-weight: 600;
     font-family: Raleway;
     cursor: pointer;
     margin-left: 1rem;
     border: none;
     font-size: 1rem;
  }
  .carret_button_container {
    position: relative;
    bottom: 3rem;
    left: 6rem;
  }
  @media screen and (max-width: 480px) {
    .cartPage_Container {
      margin: 0;
      display: flex;
      flex-direction: column;
    }
    .cartpage_name {
      font-size: 1.3rem;
    }
    .cartpage_brand {
      font-size: 1rem;
    }
    .cartpage_quantityupdate {
      width: 30px;
      height: 30px;
      margin-left: 1rem;
    }
    .cartpage_total {
      font-size: 1.5rem;
      margin-left: 2rem;
    }
  }
  